import { Chip, Stack, Typography } from '@mui/material';

import { IProject } from '../../data/projects';
import React from 'react';
import StyledTypography from '../StyledTypography';
import getVariant from '../Animation/getVariant';
import { motion } from 'framer-motion';

const Project = ({ title, technologies, description }: IProject) => {
  return (
    <>
      <motion.div variants={getVariant('fadeInRight')}>
        <Stack spacing={1}>
          <StyledTypography fontVariant="titleLight" capitalized={true}>
            {title}
          </StyledTypography>
          <Typography>{description}</Typography>
          <Stack direction="row" spacing={1}>
            {technologies.map((tech: string) => (<Chip label={tech} key={tech} />))}
          </Stack>
        </Stack>
      </motion.div>
    </>
  );
};

export default Project;
